export default {
	name: 'InfoSwiper',
	props: {
		images: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	data() {
		return {
			currentImage: 0
		}
	},
	methods: {
		change (e) {
			this.currentImage = e.detail.current
		},
		imagePriview (index) {
			const images = this.images.map(it => {
				return this.$urlPath.imageUrl + it
			})
			uni.previewImage({
				current: index,
				urls: images
			})
		}
	}
}
